<template lang="html">
  <div class="OrderDetail">
    <el-form :model="orderDetail" label-width="100px" disabled style="width: 500px;margin-left: 40px">
      <el-form-item label="产品名称：">
        <el-input v-model="orderDetail.productName"></el-input>
      </el-form-item>
      <el-form-item label="用户名称：">
        <el-input v-model="orderDetail.userName"></el-input>
      </el-form-item>
    </el-form>
  </div>
</template>

<style lang="scss" scoped>
</style>

<script>
import { mapState, mapActions } from 'vuex'

export default {

  name: 'OrderDetail',
  computed: {
    ...mapState(['orderDetail']),
  },
  methods: {
    ...mapActions(['getOrderDetail']),
  },
  created () {
    const { query } = this.$route
    const { orderId } = query

    if (orderId) {
      console.log(orderId)

      this.getOrderDetail({
        orderId,
      })
    }
  },

}
</script>
